<template lang='pug'>
  .container
    .container-box
      .introduce
        h2
    swiper.swiper-box(:options="swiperOption")
      swiper-slide(v-for="(item, index) in topPicList", :key="index")
        img(:src="item" style="width: 100%; height: 100%")
</template>
<script>
import bg1 from "@/assets/img/bg1.jpg";
import bg3 from "@/assets/img/bg3.jpg";
export default {
  name: "Index",
  data() {
    return {
      topPicList:[bg1,bg3],
      swiperOption: {
          autoplay: true,
          loop: true
        }
    };
  },
  methods: {},
  components: {}
};
</script>
<style scoped lang='stylus'>
.container
  position relative
  .container-box
    position absolute
    height 550px;
    width 100%
    z-index 2
  .swiper-box
    height 550px;
    .introduce
      text-align center
      color #fff
      font-size 40px
      h2
        margin-top 80px
</style>



